import { Component, OnInit } from '@angular/core';
import{ Router} from '@angular/router';
import { LoginComponent } from '../login/login.component';
import * as $ from 'jquery';
import { HttpClient } from "@angular/common/http";
import {UserdataService} from '../../userdata.service';
@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  usernew;
  data;
  error;
  emailPattern = "^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$";
  constructor(private router:Router,private http:HttpClient) { }
  ngOnInit() {
    $(document).ready(function()
    {
      document.body.style.zoom = "70%";
    });
  }
  onAddPost(form)
  {
    if(form.invalid)
    {
      console.log("Error");
      return false;
    }
      this.usernew={email:form.value.mail,username:form.value.username,password:form.value.pass};
      this.http.post<{message:string}>('http://localhost:3000/api/newuser',this.usernew)
      .subscribe(responseData=>
        {

          this.data=responseData;
            if(this.data.message)
            {
              this.error='';
              this.router.navigate(["/login"]);
            }
            else
            {
              this.error="UserName Already Exists.Please try another one. . . .";
            }
        });
      form.resetForm();

  }
}
