import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
@Injectable({
  providedIn: 'root'
})
export class UserdataService {
  response;
  constructor(private http: HttpClient) { }
  private _postsURL = "http://makeup-api.herokuapp.com/api/v1/products.json";
  getBooks() {
    return this
            .http
            .get(`${this._postsURL}`);

}
}
