import { Component, OnInit } from '@angular/core';
import{ Router} from '@angular/router';
import { UserComponent } from '../../user/user.component';
import * as $ from 'jquery';
import {UserdataService} from '../../userdata.service';
import { HttpClient } from '@angular/common/http';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  log;
  b;
  constructor(private router:Router,private myobj:UserdataService,private http: HttpClient) { }
  ngOnInit() {
    $(document).ready(function()
    {
      document.body.style.zoom = "72%";
    });
  }
  onAddPost(form)
  {
    if(form.invalid)
    {
      console.log("Error");
      return false;
    }
      const post={username:form.value.username,password:form.value.pass};
        this.http.post<{message:number;userdata:any}>('http://localhost:3000/api/users',post)
        .subscribe(responseData=>
          {
             if(responseData.message==1)
             {
               this.b=responseData;
              localStorage.setItem("loginToken",JSON.stringify(this.b.posts.username));
              this.router.navigate(["/user"]);
             }
             else if(responseData.message==0)
             {
               console.log("Error");
               this.log="Username or Password Invalid";
             }
          });
      form.resetForm();

  }
}
